html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-logo {
  width: 1.69em;
  height: 1em;
  fill: #fefefe;
}

@font-face {
  font-family: Casper;
  src: url("../fonts/Casper.eot?#iefix") format("embedded-opentype"), url("../fonts/Casper.woff") format("woff"), url("../fonts/Casper.ttf") format("truetype"), url("../fonts/Casper.svg#Casper") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: Casper-Bold;
  src: url("../fonts/Casper-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Casper-Bold.woff") format("woff"), url("../fonts/Casper-Bold.ttf") format("truetype"), url("../fonts/Casper-Bold.svg#Casper-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FuturaPT-Bold;
  src: url("../fonts/FuturaPT-Bold.eot?#iefix") format("embedded-opentype"), url("../fonts/FuturaPT-Bold.woff") format("woff"), url("../fonts/FuturaPT-Bold.ttf") format("truetype"), url("../fonts/FuturaPT-Bold.svg#FuturaPT-Bold") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: FuturaPT-Demi;
  src: url("../fonts/FuturaPT-Demi.eot?#iefix") format("embedded-opentype"), url("../fonts/FuturaPT-Demi.woff") format("woff"), url("../fonts/FuturaPT-Demi.ttf") format("truetype"), url("../fonts/FuturaPT-Demi.svg#FuturaPT-Demi") format("svg");
  font-weight: normal;
  font-style: normal;
}

body {
  box-sizing: border-box;
  overflow: hidden;
}

.center {
  width: 83%;
  margin: 0 auto;
  padding: 90px 0 100px 0;
  background-image: url("../img/bgi_big.jpg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 1420px) {
  .center {
    width: 95%;
  }
}

@media only screen and (max-width: 1260px) {
  .center {
    padding: 30px 0;
  }
}

.wrap {
  background-image: url("../img/bgi.png");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  position: relative;
}

.wrap:after {
  content: " ";
  display: table;
  clear: both;
}

.line {
  display: block;
  height: 7px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 0px;
  background-color: #f6cb02;
  opacity: 0;
  transition: .3s ease all;
}

.show {
  opacity: 1 !important;
}

.active {
  color: #f6cb02 !important;
}

.hidden {
  display: none;
}

.menu {
  width: 50px;
  height: 50px;
  border: 1px solid #f6cb02;
  border-radius: 5px;
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  z-index: 2;
}

.menu span {
  display: block;
  width: 80%;
  height: 3px;
  background-color: #f6cb02;
  margin: 10px auto;
  transition: .4s ease-in all;
}

.menu span.open:nth-child(1) {
  transform: rotate(45deg);
  position: relative;
  top: 13px;
}

.menu span.open:nth-child(2) {
  transform: rotate(-45deg);
}

.menu span.open:nth-child(3) {
  transform: rotate(180deg);
  opacity: 0;
}

.dark:before {
  display: block	!important;
}

.container {
  background-color: #161616;
}

.header {
  position: relative;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.logo {
  float: left;
  margin: 16px 0 16px 190px;
}

@media only screen and (max-width: 1420px) {
  .logo {
    margin: 16px 0 16px 20px;
  }
}

.logo__img {
  width: 105px;
  height: 62px;
}

.nav {
  float: right;
  padding-bottom: 15px;
  border-bottom: 1px solid #f6cb02;
  z-index: 1;
  transition: .5s ease-in all;
}

.nav:after {
  content: " ";
  display: table;
  clear: both;
}

.small {
  width: 50%;
  background-color: #000;
  border: 1px solid #f6cb02;
  border-radius: 10px;
  position: absolute;
  right: -50%;
  z-index: 2;
}

.small .nav-list {
  float: none;
  display: block;
  margin-top: 90px;
  border-top: 1px solid #f6cb02;
  border-bottom: 1px solid #f6cb02;
}

.small .nav-list__item {
  float: none;
  margin: 40px 0 40px 0;
  text-align: center;
}

.small .nav-list__link {
  float: none;
}

.small .phone {
  text-align: center;
  float: none;
}

.nav-list {
  float: left;
}

.nav-list__item {
  float: left;
  margin: 35px 45px 0 0;
}

.nav-list__link {
  color: #babab9;
  font-family: "Casper";
  font-size: 16px;
  text-transform: uppercase;
  text-decoration: none;
  padding-bottom: 14px;
  transition: .1s ease-in all;
}

.nav-list__link:hover {
  color: #f6cb02;
  border-bottom: 2px solid #f6cb02;
}

.phone {
  float: right;
  margin: 35px 110px 0 110px;
  color: #fffffe;
  font-family: "Casper";
  font-size: 16px;
}

@media only screen and (max-width: 1280px) {
  .phone {
    margin: 35px 20px 0 20px;
  }
}

.categories {
  float: left;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.categories:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1260px) {
  .categories {
    transform: translateY(0);
    position: static;
    margin-bottom: 30px;
  }
}

.categories__item {
  width: 30px;
  height: 540px;
  float: left;
  margin-right: 40px;
  position: relative;
  background-color: #f6cb02;
  vertical-align: middle;
}

@media only screen and (max-width: 1260px) {
  .categories__item {
    width: 540px;
    height: 30px;
    float: none;
    display: block;
    margin: 10px auto;
  }
}

.categories__name {
  color: #fff;
  font-size: 20px;
  writing-mode: tb-rl;
  position: absolute;
  bottom: 5px;
  transform: rotate(180deg);
  text-transform: uppercase;
  font-family: "FuturaPT-Bold";
  color: #000;
  font-size: 30px;
  line-height: 39px;
}

@media only screen and (max-width: 1260px) {
  .categories__name {
    transform: rotate(0);
    writing-mode: horizontal-tb;
    line-height: 15px;
  }
}

.main {
  float: right;
  background-color: #000;
  overflow: hidden;
}

.main:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 1260px) {
  .main {
    width: 100%;
  }
}

.content {
  float: left;
}

@media only screen and (max-width: 720px) {
  .content {
    width: 100%;
  }
}

.content__caption {
  font-size: 150px;
  color: #1a1a1a;
  text-transform: uppercase;
  padding: 0 10px;
  font-family: "FuturaPT-Bold";
  line-height: 90px;
}

@media only screen and (max-width: 1800px) {
  .content__caption {
    font-size: 120px;
  }
}

@media only screen and (max-width: 1070px) {
  .content__caption {
    display: none;
  }
}

.content__list {
  width: 100%;
  padding: 100px 0 50px 140px;
}

@media only screen and (max-width: 1800px) {
  .content__list {
    padding: 100px 0 50px 30px;
  }
}

@media only screen and (max-width: 1070px) {
  .content__list {
    padding: 40px 0 0px 30px;
  }
}

@media only screen and (max-width: 720px) {
  .content__list {
    text-align: center;
  }
}

.content__item {
  margin-bottom: 40px;
  position: relative;
}

.content__link {
  font-family: 'FuturaPT-Bold';
  font-size: 30px;
  text-decoration: none;
  text-transform: uppercase;
  color: #808080;
  transition: .3s ease all;
}

.content__link:hover {
  color: #fff;
}

@media only screen and (max-width: 960px) {
  .content__link {
    font-size: 15px;
  }
}

.info {
  width: 420px;
  background-image: url("../img/docs.jpg");
  float: right;
  position: relative;
  transition: .3s ease-in all;
}

.info:before {
  content: '';
  width: 100%;
  height: 100%;
  display: none;
  background-color: rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 1600px) {
  .info {
    width: 300px;
  }
}

@media only screen and (max-width: 720px) {
  .info {
    width: 80%;
    float: none;
    position: absolute;
    top: 43%;
    left: 10%;
    opacity: 0;
    z-index: -1;
  }
}

.info__text {
  position: absolute;
  opacity: 0;
  font-family: 'FuturaPT-Demi';
  color: #fff;
  font-size: 18px;
  line-height: 18px;
  width: 60%;
  left: 20%;
  top: 200px;
  transition: 0.3s ease-in all;
}

@media only screen and (max-width: 960px) {
  .info__text {
    top: 150px;
  }
}

.close {
  position: absolute;
  right: 15px;
  top: 30px;
  opacity: 0;
  cursor: pointer;
}

.close span {
  width: 40px;
  height: 10px;
  background-color: #f6cb02;
  display: block;
}

.close span:first-child {
  transform: rotate(45deg);
}

.close span:nth-child(2) {
  transform: rotate(-45deg);
  position: relative;
  top: -10px;
}
